.stage-cube-cont{
    padding: 100px;
    overflow: hidden;
    height: 400px;
    width: 400px;
    align-items: center;
}

.stage-cube-cont .cubespinner{
    animation-name: spincube;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-duration: 12s;
    transform-style: preserve-3d;
    transform-origin: 100px 100px 0;
    margin-left: calc(50% - 100px);
}

.stage-cube-cont .cubespinner div{
    position: absolute;
    width: 200px;
    height: 200px;
    border: 1px solid #ccc;
    background: rgba(255, 255, 255, 0.4);
    font-size: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 20px 0px lightyellow;
}

.stage-cube-cont .cubespinner .face1{
    transform: translateZ(100px);
    color: #dd0031;
}
.stage-cube-cont .cubespinner .face2{
    transform: rotateY(90deg) translateZ(100px);
}
.stage-cube-cont .cubespinner .face3{
    transform: rotateY(90deg) rotateX(90deg) translateZ(100px);
}
.stage-cube-cont .cubespinner .face4{
    transform: rotateY(180deg) rotateZ(90deg) translateZ(100px);
}
.stage-cube-cont .cubespinner .face5{
    transform: rotateY(-90deg) rotateZ(90deg) translateZ(100px);
}
.stage-cube-cont .cubespinner .face6{
    transform: rotateX(90deg) translateZ(-100px);
}
@keyframes spincube {
    from,
    to{
        transform: rotateX(0deg) rotateY(0deg) rotateZ(0deg);
    }
    16% {
        transform: rotateY(-90deg) rotateZ(90deg);
      }
      33% {
        transform: rotateY(-90deg) rotateX(90deg);
      }
      50% {
        transform: rotateY(-180deg) rotateZ(90deg);
      }
      66% {
        transform: rotateY(-270deg) rotateX(90deg);
      }
      83% {
        transform: rotateX(90deg);
      }
}